//
// Form
//
.my-form {
  .form__title {
    margin-bottom: 2rem;
  }
  .form__bottom {
    text-align: center;

    .form__bottom--toolbar {
      .form__bottom-toolbar--button {
        margin-right: 1rem;
      }
    }
  }
}

.kt-form {
  .kt-form__group {
    &.kt-form__group--inline {
      display: table;
      padding: 0;

      > .kt-form__label {
        display: table-cell;
        vertical-align: middle;
        margin: 0;
        padding-right: 1rem;

        &.kt-form__label-no-wrap {
          white-space: nowrap;
        }

        > label {
          margin: 0;
        }
      }

      > .kt-form__control {
        display: table-cell;
        vertical-align: middle;
        margin: 0;
        width: 100%;
      }
    }
  }

  // Action bar
  .kt-form__actions {
    &.kt-form__actions--right {
      text-align: right;
    }

    &.kt-form__actions--center {
      text-align: center;
    }
  }

  &.kt-form--center {
    width: 60%;
    margin: 0 auto;
  }
}

// Validation feedback
.is-valid {
  .valid-feedback {
    display: inline-block;
  }
}

.is-invalid {
  .invalid-feedback {
    display: inline-block;
  }
}

@include kt-desktop {
  .kt-form {
    &.kt-form--label-right {
      .form-group {
        label:not(.kt-checkbox):not(.kt-radio):not(.kt-option) {
          text-align: right;
        }
      }
    }
  }
}
